namespace airkit {
    /**
     * 
     * @author ankye
     * @time 2017-7-13
     */
    export class SceneManager {
        public static scenes: SDictionary<any> = new SDictionary<any>();
        /**
         * idname
         * @param name
         * @param cls
         */
        public static register(
            name: string,
            cls: any
        ): any {
           SceneManager.scenes.add(name, cls);
           ClassUtils.regClass(name, cls);
        }

        private _curScene: BaseView;

        private static instance: SceneManager = null;
        public static get Instance(): SceneManager {
            if (!this.instance) this.instance = new SceneManager();
            return this.instance;
        }

        public setup(): void {
            this.registerEvent();
        }

        public destroy(): void {
            this.unRegisterEvent();
        }

        public update(dt: number): void {
            //do update
            if (this._curScene) {
                this._curScene.update(dt);
            }
        }

        private registerEvent(): void {
            EventCenter.on(EventID.CHANGE_SCENE, this, this.onChangeScene);
            EventCenter.on(EventID.RESIZE, this, this.resize);
        }
        private unRegisterEvent(): void {
            EventCenter.off(EventID.CHANGE_SCENE, this, this.onChangeScene);
            EventCenter.off(EventID.RESIZE, this, this.resize);
        }

        private resize(): void {
            Log.info(
                "SceneManager Receive Resize {0} {1}",
                cc.winSize.width,
                cc.winSize.height
            );

            if (this._curScene) {
                this._curScene.setSize(
                    fgui.GRoot.inst.width,
                    fgui.GRoot.inst.height
                );
                var func: Function = this._curScene["resize"];
                let result = null;
                if (func) {
                    result = func.apply(this._curScene);
                }
            }
        }
        private onChangeScene(evt: EventArgs): void {
            let info: any = evt.get(0);
            this.gotoScene(info);
        }
        //~//

        private onComplete(v: any): void {
            this._curScene = v;
        }
        /***/
        public gotoScene(sceneName: string, args?: any): void {
            this.exitScene();
           // let clas = SceneManager.scenes.getValue(sceneName);
            //

            let clas = ClassUtils.getClass(sceneName);

            let scene = new clas();
            scene["__scene_type__"] = sceneName;
            scene.setup(args);
            scene.loadResource(ResourceManager.DefaultGroup, clas)
                .then((v) => {
                    this.onComplete(v);
                    LayerManager.mainLayer.addChild(scene.sceneView);
                })
                .catch((e) => {
                    Log.error(e);
                });

            
        }

        private exitScene(): void {
            if (this._curScene) {
                //
                let sceneName = //SceneManager.scenes.getValue(
                    this._curScene["__scene_type__"]
               // );
                let clas = ClassUtils.getClass(sceneName);
                clas.unres();
                this._curScene.removeFromParent();
                this._curScene.dispose();
                this._curScene = null;
                UIManager.Instance.closeAll();
                ObjectPools.clearAll();
            }
        }
    }
}
